.app-wrapper {
    margin: 0px auto;
    display: grid;
    width: 1200px;
    grid-template-rows: 60px 1fr;
    grid-auto-columns: 2fr 10fr;
    grid-template-areas:
            "h h"
            "n c";
    /*grid-gap: 10px;*/
}

.app-wrapper-content {
    grid-area: c;
    background-color: coral;
}

.content img {
    width: 800px;
}




